/* styles.css */

/* Estilo para centrar el título y darle fondo negro */
.title {
  background-color: #ebc20f; /* Fondo negro */
  color: black; /* Texto blanco */
  text-align: center; /* Centrar el texto */
  padding: 20px; /* Espaciado interno */
  margin-top: 0; /* Eliminar margen superior */
}

/* Estilo para el contenedor de las tarjetas de clientes */
.clientes-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-top: 20px;
}

/* Estilo para cada tarjeta de cliente */
.cliente-card {
  background-color: pink;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 250px;
  text-align: left;
  transition: transform 0.3s ease-in-out;
}

/* Cambio de tamaño cuando se pasa el ratón sobre la tarjeta */
.cliente-card:hover {
  transform: scale(1.05);
}

/* Estilo de los títulos dentro de cada tarjeta */
.cliente-card h3 {
  color: orange;
  margin-bottom: 10px;
}

/* Estilo para los párrafos dentro de la tarjeta */
.cliente-card p {
  font-size: 16px;
  color: #333;
  margin: 8px 0;
}

.cliente-card strong {
  color: blueviolet;
}

/* Estilo para el título de la gráfica */
h2 {
  text-align: center;  
  margin-top: 40px;
  color: red;
}
.grafica-container {
  justify-content: center;
  width: 50%;  /* Ajusta este valor según el tamaño deseado */
  height: 300px;  /* Ajusta la altura */
  margin: auto;
}
.status-button {
  padding: 10px 20px;
  border: none;
  color: white;
  font-weight: bold;
  cursor: pointer;
  margin-top: 10px;
}

.status-button.Aprobado {
  background-color: rgb(21, 198, 211);
  color: white;
}

.status-button.Reprobado {
  background-color: #f4369e;
  color: white;
}
.grafica-container {
  display: flex;
  justify-content: center;
  align-items: center; /* Si también quieres centrarla verticalmente */
  width: 100%; /* Asegúrate de que el contenedor tenga suficiente espacio */
}

.centered-title {
  text-align: center;
}
.cliente-card {
  width: 350px;  /* Ancho ajustado */
  height: auto;  /* Ajuste automático para evitar que las tarjetas sean demasiado altas */
  margin: 20px;
  padding: 20px;
  background-color: pink;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  
}

.clientes-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
}
@media (max-width: 768px) {
  .cliente-card {
    width: 90%;  /* Hacer que las tarjetas sean más pequeñas en pantallas móviles */
  }

  .grafica-container {
    width: 90%;  /* Ajustar el tamaño de la gráfica en pantallas móviles */
  }
}
